.navigationMb{
  position: fixed;
  width: 100%;
  bottom: 0;
  display: block;
  z-index: 300;
  background: #fff;
  box-shadow: 0px 1px 20px 0px #14141442;
  .navigation__contain{
    .navigation__list{
      display: flex;
      justify-content: space-between;
      .navigation__item{
        font-size: 32px;
        padding: 5px 10px;
        filter: drop-shadow(0 0 0.75rem #14141482);
        #cart{
          position: relative;
          #cartNumber{
            position: absolute;
            top: 44%;
            left: 50%;
            font-size: 15px;
            color: #c40000;
            display: inline-block;
            font-weight: 800;
            margin-left: 5px;
            transform: translate(-50%,-50%);
            background: #fff;
            width: 18px;
            height: 18px;
            text-align: center;
            border-radius: 50%;
          }
        }
      }
    }
  }
}
