.product__list{
  .product__item{
    box-shadow: 0px 1px 1px 1px #1414144a;
    margin: 5px 10px;
    .product__item-img{
      position: relative;
      max-height: 390px;
      overflow: hidden;
      img{
        opacity: 1;
        transform: scale(1);
        transition: all 0.25s linear;
      }
      .lastest__config{
        position: absolute;
        bottom: 5px;
        left: 50%;
        transform: translateX(-50%);
        display: none;
        @include MediaQuery($xs){
            display: flex;
        };
        .btn{
          padding: 10px 15px;
          margin: 10px 5px;
          box-shadow: 1px 2px 1px 0px #141414;
          color: var(--primary-color);
          background: #fff;
          transition: all 0.25s linear;
          @include MediaQuery($xs){
            background: var(--primary-bg-color);
            color: #fff;
          };
          &:hover,&:focus{
            box-shadow: inset 1px 2px 1px 0px #141414;
            background: var(--primary-bg-color);
            color: #fff;
          }
          &.viewQuick{
            @include MediaQuery($xs){
              background: #c40000;
            };
          }
          &.viewQuick:hover,&.viewQuick:focus{
            background: #c40000;
          }
        }
        }
    }
    .product__item-title{
      padding: 20px 20px 5px 20px;
      text-align: center;
      min-height: 85px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      line-height: 1.5;
    }
    .product__item-price{
      text-align: center;
      font-weight: 500;
      padding: 5px 20px 20px 20px;
      font-size: 18px;
      height: 89px;
    }
    &:hover{
      box-shadow: 0px 1px 11px 1px #14141459;
      .product__item-img{
        img{
          opacity: 0.85;
          transform: scale(1.1);
        }
        .lastest__config{
          display: flex;
        }
      }
    }
  }
}
